import { Button, Form, Input, InputNumber, message, Modal, Radio, Select } from 'antd';
import logo from 'assets/images/logo.png';
import { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { gameClearAction, gameUpdateAction } from 'redux/action/game.action';
import { socketConnectAction, socketDisconnectAction } from 'redux/action/socket.action';
import { io } from 'socket.io-client';
import { CallbackError } from 'types/callback-error.interface';
import x from 'assets/images/x.svg';
import o from 'assets/images/o.svg';

export const Home: React.FC<{}> = () => {
    const [modalOpen, setModalOpen] = useState<string>();

    const dispatch = useDispatch();

    const [formRandom] = Form.useForm();
    const [formCreate] = Form.useForm();
    const [formPve] = Form.useForm();
    const [formJoin] = Form.useForm();
    const [formSpectate] = Form.useForm();

    const navigate = useNavigate();

    useEffect(() => {
        dispatch(socketDisconnectAction());
        dispatch(gameClearAction());
    }, []);

    const closeModal = () => {
        setModalOpen(undefined);
    };

    const handleJoinRoomPvP = (values: any, joinEvent: string, path = '/play') => {
        const client = io(process.env.SERVER_URL + '/pvp', { transports: ['websocket'] });

        if (values.name) localStorage.setItem('name', values.name);

        client.emit(joinEvent, values, (err: CallbackError, data: any) => {
            if (err) {
                return message.error(err.message);
            }

            const { room, you, time } = data;
            dispatch(gameUpdateAction({ room, you, time }));

            navigate(path);
        });

        client.on('player_join', (data: any) => {
            const { x, o, turn, map, time } = data;
            dispatch(gameUpdateAction({ x, o, turn, map, time }));
        });

        client.on('turn', (data: any) => {
            const { x, o, turn, map, time } = data;
            dispatch(gameUpdateAction({ x, o, turn, map, time }));
        });

        dispatch(socketConnectAction({ client }));
    };

    const handleRandom = async () => {
        try {
            const values = await formRandom.validateFields();
            handleJoinRoomPvP(values, RANDOM);
        } catch (err: any) {
            return;
        }
    };

    const handleCreate = async () => {
        try {
            const values = await formCreate.validateFields();
            handleJoinRoomPvP(values, CREATE);
        } catch (err: any) {
            return;
        }
    };

    const handleJoin = async () => {
        try {
            const values = await formJoin.validateFields();
            handleJoinRoomPvP(values, JOIN);
        } catch (err: any) {
            return;
        }
    };

    const handleSpectate = async () => {
        try {
            const values = await formSpectate.validateFields();
            handleJoinRoomPvP(values, SPECTATE, '/spectate');
        } catch (err: any) {
            return;
        }
    };

    const handlePve = async () => {
        try {
            const values = await formPve.validateFields();

            const client = io(process.env.SERVER_URL + '/pve', { transports: ['websocket'] });

            if (values.name) localStorage.setItem('name', values.name);

            client.emit('join', values, (err: CallbackError, data: any) => {
                if (err) {
                    return message.error(err.message);
                }

                const { room, you, time } = data;
                dispatch(gameUpdateAction({ room, you, time }));

                navigate('/play');
            });

            client.on('player_join', (data: any) => {
                const { x, o, turn, map, time } = data;
                dispatch(gameUpdateAction({ x, o, turn, map, time }));
            });

            client.on('turn', (data: any) => {
                const { x, o, turn, map, time } = data;
                dispatch(gameUpdateAction({ x, o, turn, map, time }));
            });

            dispatch(socketConnectAction({ client }));
        } catch (err: any) {
            return;
        }
    };

    return (
        <div className="home">
            <div className="welcome mb-24">Welcome to</div>

            <div className="left-buttons buttons">
                <Button type="primary" onClick={() => setModalOpen(RANDOM)}>
                    PLAY ONLINE
                </Button>
                <Button type="primary" onClick={() => setModalOpen(CREATE)}>
                    CREATE ROOM
                </Button>
                <Button type="primary" onClick={() => setModalOpen(PVE)}>
                    VS COMPUTER
                </Button>
            </div>

            <div className="logo mb-24">
                <img src={logo} alt="" />
            </div>

            <div className="right-buttons buttons">
                <Button type="primary" onClick={() => setModalOpen(JOIN)}>
                    JOIN ROOM
                </Button>
                <Button type="primary" onClick={() => setModalOpen(SPECTATE)}>
                    SPECTATE
                </Button>
            </div>

            <Modal title="Play online" open={modalOpen === RANDOM} onCancel={closeModal} onOk={handleRandom}>
                <Form form={formRandom} layout="vertical" initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Create room" open={modalOpen === CREATE} onCancel={closeModal} onOk={handleCreate}>
                <Form
                    form={formCreate}
                    layout="vertical"
                    initialValues={{ name: localStorage.getItem('name'), time: 60 }}
                >
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                    <Form.Item label="Time limit" name="time" rules={[{ required: true, message: 'Time is required' }]}>
                        <Select options={timeOptions} />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Play vs computer" open={modalOpen === PVE} onCancel={closeModal} onOk={handlePve}>
                <Form
                    form={formPve}
                    layout="vertical"
                    initialValues={{ name: localStorage.getItem('name'), role: 'x' }}
                >
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                    <Form.Item label="You" name="role" rules={[{ required: true, message: 'Role is required' }]}>
                        <Radio.Group className="role-select">
                            <Radio.Button value="x" type="ghost">
                                <img src={x} width={18} alt="" />
                            </Radio.Button>
                            <Radio.Button value="o">
                                <img src={o} width={18} alt="" />
                            </Radio.Button>
                        </Radio.Group>
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Join room" open={modalOpen === JOIN} onCancel={closeModal} onOk={handleJoin}>
                <Form form={formJoin} layout="vertical" initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                    <Form.Item label="Room" name="room" rules={[{ required: true, message: 'Room ID is required' }]}>
                        <InputNumber type="number" controls={false} placeholder="Type room ID here" />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Spectate" open={modalOpen === SPECTATE} onCancel={closeModal} onOk={handleSpectate}>
                <Form
                    form={formSpectate}
                    layout="vertical"
                    initialValues={{ name: localStorage.getItem('name'), time: 60 }}
                >
                    <Form.Item label="Room" name="room" rules={[{ required: true, message: 'Room ID is required' }]}>
                        <InputNumber type="number" controls={false} placeholder="Type room ID here" />
                    </Form.Item>
                    <Form.Item label="Time limit" name="time" rules={[{ required: true, message: 'Time is required' }]}>
                        <Select options={timeOptions} />
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    );
};

const RANDOM = 'join_random';
const CREATE = 'create';
const PVE = 'join_pve';
const JOIN = 'join';
const SPECTATE = 'spectate';

const timeOptions = [
    { label: '5s', value: 5 },
    { label: '15s', value: 15 },
    { label: '30s', value: 30 },
    { label: '60s', value: 60 },
];
